import {Component, OnInit} from '@angular/core';
import {Authentication} from '../_services/authentication';
import {Router, ActivatedRoute} from '@angular/router';
import {User} from '../_models/user';

@Component({
    /*  selector: 'app-login', */
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

    error: string;
    model: any = {'email': 'user@presencehealth.org', 'password': 'password'};
    returnUrl: string;

    constructor(private authentication: Authentication,
                private router: Router,
                private route: ActivatedRoute) {
    }

    ngOnInit() {
        this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || 'steps';
     //   this.returnUrl = (this.returnUrl == 'clear-profile') ? 'steps' : this.returnUrl;
        this.authentication.logout();
    }

    login(): void {
        this.authentication.login(this.model.email, this.model.password).subscribe(
            (user: User) => {
                this.router.navigate([this.returnUrl]);
            },
            e => {
                this.error = e;
                console.log('error', e);
            },
            () => {
                console.log('complete?');
            }
        );
        return null;
    }

}



